<template>
  <div>
    <el-row :gutter="15" class="overview">
      <el-col :md="6" :sm="12" class="m-b">
        <el-card class="box-card bg-primary" :body-style="{padding: 0}">
          <div class="chart" ref="traffics"></div>
          <div class="count">
            <small>Website Traffics</small>
            <h2>{{traffics}}</h2>
          </div>
        </el-card>
      </el-col>
      <el-col :md="6" :sm="12" class="m-b">
        <el-card class="box-card bg-success overview" :body-style="{padding: 0}">
          <div class="chart" ref="impressions"></div>
          <div class="count">
            <small>Website Impressions</small>
            <h2>{{impressions}}</h2>
          </div>
        </el-card>
      </el-col>
      <el-col :md="6" :sm="12" class="m-b">
        <el-card class="box-card bg-warning overview" :body-style="{padding: 0}">
          <div class="chart" ref="sales"></div>
          <div class="count">
            <small>Total Sales</small>
            <h2>{{sales}}</h2>
          </div>
        </el-card>
      </el-col>
      <el-col :md="6" :sm="12" class="m-b">
        <el-card class="box-card bg-danger overview" :body-style="{padding: 0}">
          <div class="chart" ref="supports"></div>
          <div class="count">
            <small>Support Tickets</small>
            <h2>{{supports}}</h2>
          </div>
        </el-card>
      </el-col>
    </el-row>
  </div>
</template>

<style>
  @import "../css/variables.css";

  @define-mixin chart $color {
    .bg-$color .count {
      background-color: color(var(--color-$color) tint(20%));

      &:before {
        background-color: var(--color-$color);
      }
    }
  }
  
  .overview {
    .chart, .count {
      padding: 15px;
      height: 80px;
      box-sizing: border-box;
      color: #fff;
    }

    .el-card__body {
      position: relative;
    }

    .chart {
      float: left;
      width: 100px;
    }

    .count {
      position: relative;
      margin-left: 100px;

      small, h2 {
        position: relative;
        z-index: 1;
        margin: 0;
        padding: 0;
        white-space: nowrap;
        overflow: hidden;
        text-overflow: ellipsis;
        display: block;
      }

      small {
        font-size: 12px;
      }

      h2 {
        font-size: 22px;
        font-weight: normal;
      }

      &:before {
        content: "";
        position: absolute;
        top: 0;
        bottom: 0;
        left: 0;
        right: 0;
        width: 0;
      }
    }

    @each $color in (primary, success, warning, danger) {
      @mixin chart $color
    }

    .el-card:hover .count:before {
      width: 100%;
      transition: width;
      transition-duration: .7s;
    }
  }
</style>

<script>
  import * as d3 from 'd3'

  export default {
    name: 'dashboard',
    data() {
      return {
        trafficsData: [102860, 123432, 144004, 41144, 0, 82288, 41144, 82288, 164577, 41144, 61716, 61716, 41146],
        impressionsData: [7433000, 14866000, 44598000, 29732000, 0, 59464000, 14866000, 29732000, 37165000, 22299000, 7433000, 52031000, 37166000],
        salesData: [45183, 46920, 46341, 26067, 19695, 6951, 23171, 45183, 46920, 46341, 26067, 19695, 6951, 23171, 6951, 23171],
        supportsData: ['14622', '9234']
      }
    },
    computed: {
      traffics() {
        return d3.format(',')(d3.sum(this.trafficsData))
      },
      impressions() {
        return d3.formatPrefix(',.0', 1e3)(d3.sum(this.impressionsData))
      },
      sales() {
        return d3.format('$,')(d3.sum(this.salesData))
      },
      supports() {
        return d3.format(',')(d3.sum(this.supportsData))
      }
    },
    methods: {
      renderTraffics() {
        const data = this.trafficsData

        let x = d3.scaleBand().domain(d3.range(data.length)).rangeRound([0, 70]).padding(0.3)
        let y = d3.scaleLinear().domain([0, d3.max(data)]).rangeRound([0, 50])

        let svg = d3.select(this.$refs.traffics).append('svg').attr('height', '100%').attr('width', '100%')

        svg.selectAll('rect').data(data)
          .enter().append('rect')
          .attr('fill', '#fff')
          .attr('x', (d, i) => x(i))
          .attr('y', d => 50 - y(d))
          .attr('width', x.bandwidth())
          .attr('height', d => Math.round(y(d)))
      },
      renderImpressions() {
        const data = this.impressionsData

        let x = d3.scaleLinear().domain([0, data.length]).rangeRound([0, 70])
        let y = d3.scaleLinear().domain([0, d3.max(data)]).rangeRound([0, 50])
        let line = d3.line().x((d, i) => x(i)).y(d => 50 - y(d))

        let svg = d3.select(this.$refs.impressions).append('svg').attr('height', '100%').attr('width', '100%')

        svg.append('g').selectAll('path.line').data([data])
          .enter().append('path').attr('class', 'line')
          .attr('fill', 'none')
          .attr('stroke', '#fff')
          .attr('d', d => line(d))
      },
      renderSales() {
        const data = this.salesData

        let x = d3.scaleBand().domain(d3.range(data.length)).rangeRound([0, 70]).padding(0.3)
        let y = d3.scaleLinear().domain([0, d3.max(data)]).rangeRound([0, 50])

        let svg = d3.select(this.$refs.sales).append('svg').attr('height', '100%').attr('width', '100%')

        svg.selectAll('rect').data(data)
          .enter().append('rect')
          .attr('fill', '#fff')
          .attr('x', (d, i) => x(i))
          .attr('y', d => 50 - y(d))
          .attr('width', x.bandwidth())
          .attr('height', d => Math.round(y(d)))
      },
      renderSupports() {
        const data = this.supportsData
        let arcs = d3.pie().padAngle(0.1)
        let arc = d3.arc().outerRadius(25).innerRadius(12)

        let svg = d3.select(this.$refs.supports).append('svg').attr('height', '100%').attr('width', '100%')

        svg.append('g').attr('transform', 'translate(35, 25)')
          .selectAll('path.arc').data(arcs(data)).enter().append('path').attr('class', 'arc')
          .attr('fill', (d, i) => ['#fff', '#ff8080'][i % 2])
          .attr('stroke', 'none')
          .attr('d', d => arc(d))
      }
    },
    mounted() {
      this.renderTraffics()
      this.renderImpressions()
      this.renderSales()
      this.renderSupports()
    }
  }
</script>


